100%;\n    display: block;\n}\n\n/* Natural flow and rhythm in articles by default */\narticle>*+* {\n    margin-top: 1em;\n}\n\n/* Inherit fonts for inputs and buttons */\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\n/* Blur images when they have no alt attribute */\nimg:not([alt]) {\n    filter: blur(10px);\n}\n\n/* Remove all animations and transitions for people that prefer not to see them */\n@media (prefers-reduced-motion: reduce) {\n    * {\n        animation-duration: 0.01ms !important;\n        animation-iteration-count: 1 !important;\n        transition-duration: 0.01ms !important;\n        scroll-behavior: auto !important;\n    }\n}","/**\nThe function `map-deep-get` is a custom function written in a Sass/SCSS stylesheet. Its purpose is to retrieve a nested value from a map (also known as a dictionary or associative array) using a variable number of keys.\n\nHere's a breakdown of how the function works:\n\n1. The function is defined using the `@function` directive and named `map-deep-get`. It takes two parameters:\n   - `$map`: The map from which the value will be retrieved.\n   - `$keys...`: A variable number of keys that specify the path to the desired value in the map.\n\n2. Inside the function, there is a `@each` loop that iterates over each `$key` provided in `$keys...`.\n\n3. Within the loop, the `map-get()` function is used to retrieve the value associated with the current `$key` from the `$map`. The retrieved value is then assigned back to the `$map` variable, effectively updating it to the nested map.\n\n4. After the loop finishes, the final value of `$map` is returned using the `@return` directive.\n\nIn summary, the `map-deep-get` function allows you to access a nested value within a map by providing a sequence of keys that define the path to the desired value. Each key is used to access a nested map until the final value is reached, which is then returned.\n*/\n\n@function map-deep-get($map, $keys...) {\n\t@each $key in $keys {\n\t\t$map: map-get($map, $key);\n\t}\n\t@return $map;\n}",".container {\n  position: relative;\n  margin: 0 auto;\n  width: 100%;\n  padding: 0 $size--md;\n  box-sizing: border-box;\n\n  @include media-breakpoint('sm') {\n    max-width: $col--sm;\n  }\n\n  @include media-breakpoint('md') {\n    max-width: $col--md;\n  }\n\n  @include media-breakpoint('lg') {\n    max-width: $col--lg;\n  }\n\n  @include media-breakpoint('xl') {\n    max-width: $col--xl;\n  }\n}\n","@mixin media-breakpoint($breakpoint, $property: min-width) {\n  @media ($property: map-deep-get($breakpoints, $breakpoint, breakpoint)) { @content; }\n}\n\n",".full-bleed {\n    margin-left: calc((100vw - 100%) / -2);\n    width: 100vw;\n}\n",".flow > * + * {\n    margin-top: var(--flow-space, 1em);\n}",".visually-hidden {\n    border: 0;\n    clip: rect(0 0 0 0);\n    height: auto;\n    margin: 0;\n    left: -10000px;\n    opacity: 0;\n    transform: scale(0);\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n    white-space: nowrap;\n  }",".block-padding {\n    padding-top: $spacing--lg;\n    padding-bottom: $spacing--lg;\n\n    @include media-breakpoint('md') {\n        padding-top: $spacing--xl;\n        padding-bottom: $spacing--xl;\n    }\n}\n\n.block-padding--top {\n    padding-top: $spacing--lg;\n\n    @include media-breakpoint('md') {\n        padding-top: $spacing--xl;\n    }\n}\n\n.block-padding--bottom {\n    padding-bottom: $spacing--lg;\n\n    @include media-breakpoint('md') {\n        padding-bottom: $spacing--xl;\n    }\n}","/**\n * Background Colours\n * -----------------------------------------------------------------------------\n */\n\n.bg--red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--dark-red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--light-red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--off-white {\n    background-color: $pf-off-white;\n    color: $pf-charcoal;\n}\n\n.bg--white {\n    background-color: $pf-white;\n    color: $pf-charcoal;\n}\n\n.bg--charcoal {\n    background-color: $pf-charcoal;\n    color: $pf-white;\n}\n\n.bg--yellow {\n    background-color: $pf-yellow;\n    color: $pf-charcoal;\n}\n\n.bg--yellow-alt {\n    background: $pf-yellow--alt;\n    color: $pf-charcoal;\n}\n\n.bg--raspberry {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--dark-green {\n    background-color: $pf-dark-green;\n    color: $pf-white;\n}\n\n/**\n * Text Colours\n * -----------------------------------------------------------------------------\n */\n.red {\n    color: $pf-raspberry;\n}\n\n.dark-red {\n    color: $pf-raspberry;\n}\n\n.light-red {\n    color: $pf-raspberry;\n}\n\n.off-white {\n    color: $pf-off-white;\n}\n\n.white {\n    color: $pf-white;\n}\n\n.charcoal {\n    color: $pf-charcoal;\n}",".overlay {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.3);\n}","// Primary font - Jost (weights 0 - 800)\n@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');\n\n// Secondary font - Katibeh\n@import url('https://fonts.googleapis.com/css2?family=Katibeh&display=swap');\n\np,\nli,\na {\n  font-family: $font--primary;\n  font-size: $size--md;\n  font-weight: 300;\n  line-height: $spacing--md;\n}\n\n.text__standfirst {\n  li, p, a {\n    font-family: $font--primary;\n    font-size: $size--lg;\n    font-weight: 400;\n    line-height: $spacing--md;\n    padding-bottom: 1.5rem;\n  }\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: $font--primary;\n  text-transform: uppercase;\n  font-weight: 700;\n}\n\n.h1,\n.h2,\n.h3,\n.h4,\n.h5,\n.h6 {\n  font-family: $font--primary;\n}\n\nh2 {\n  letter-spacing: 2px;\n}\n\n.giant-h1 {\n\n  font-family: $font--primary;\n  font-weight: 600;\n  font-size: $size--xxl;\n  line-height: 1.2;\n\n  @include media-breakpoint('lg') {\n    font-size: $size--giant;\n    line-height: 1.2;\n  }\n}\n\n.h1,\nh1:not([class]),\n.heading__h1 {\n  font-size: $size--xxl;\n}\n\n.h2,\nh2:not([class]),\n.heading__h2 {\n  font-size: $size--xl;\n}\n\n.h3,\nh3:not([class]),\n.heading__h3 {\n  font-size: $size--lg;\n}\n\n.h4,\nh4:not([class]),\n.heading__h4 {\n  font-size: $size--md;\n}\n\n.h5,\nh5:not([class]),\n.heading__h5 {\n  font-size: $size--base;\n}\n\n.h6,\nh6:not([class]),\n.heading__h6 {\n  font-size: $size--sm;\n}\n\na {\n  color: $pf-raspberry;\n  text-decoration: underline;\n}\n\nul:not(.acf-radio-list):not(.acf-tab-group) {\n  li:not([class]) {\n    list-style: none;\n  }\n\n  li:not([class]):not(.editor-styles-wrapper):before {\n    content: \"·\";\n    font-size: 30px;\n    vertical-align: middle;\n    line-height: 20px;\n    padding-right: 0.5rem;\n  }\n}\n\nol {\n  margin-left: 1rem;\n\n  li {\n    list-style-type: decimal;\n\n    li {\n      margin-left: 1rem;\n    }\n  }\n}\n\n.button {\n  font-size: rem(18);\n  letter-spacing: 2px;\n\n  @include media-breakpoint('lg') {\n    font-size: rem(22);\n  }\n}\n\n.nav {\n  ul {\n    li {\n      a {\n        font-size: $size--base;\n        text-decoration: none;\n      }\n    }\n  }\n}\n\n.quote {\n  &__short {\n    font-size: $size--lg;\n    font-weight: 600;\n    line-height: $spacing--md;\n    text-transform: uppercase;\n    width: 40%;\n    margin: auto;\n    margin-top: 4rem;\n\n    &::before {\n      content: \"“\";\n      font-size: 100px;\n      position: absolute;\n      left: 17rem;\n      color: $pf-raspberry;\n    }\n\n    &::after {\n      content: \"”\";\n      font-size: 100px;\n      position: absolute;\n      right: 17rem;\n      color: $pf-raspberry;\n    }\n  }\n\n  &__long {\n    font-size: $size--lg;\n    font-weight: 400;\n    line-height: $spacing--md;\n    width: 40%;\n    margin: auto;\n    margin-top: 4rem;\n\n    &::before {\n      content: \"“\";\n      font-size: 100px;\n      position: absolute;\n      left: 17rem;\n      color: $pf-raspberry;\n    }\n\n    &::after {\n      content: \"”\";\n      font-size: 100px;\n      position: absolute;\n      right: 17rem;\n      color: $pf-raspberry;\n    }\n  }\n\n  &__author {\n    font-size: $size--lg;\n    font-weight: 400;\n    line-height: $spacing--md;\n    width: 40%;\n    margin: auto;\n    margin-top: 2rem;\n    color: $pf-raspberry;\n  }\n}\n\n\n\n@include media-breakpoint('lg') {\n  .nav {\n    font-size: $size--md;\n\n    .menu-item:hover {\n      a {\n        color: $pf-white;\n      }\n    }\n\n    .sub-menu {\n      font-size: $size--md;\n    }\n  }\n}","// // Header specific variables\n// $header-height-mobile: 78px;\n// $header-height-desktop: 60px;\n\n.caret {\n  background-image: url('../../../images/icons/caret_white.svg');\n  height: 25px;\n  width: 25px;\n  background-repeat: no-repeat;\n  background-position: center;\n  transform: rotate(0);\n  transition: rotate 300ms ease-out;\n  display: inline-block;\n\n  @include media-breakpoint('lg') {\n    background-image: url('../../../images/icons/caret_charcoal.svg');\n  }\n}\n\n.header {\n  position: sticky;\n  top: 0;\n  height: $header-height-mobile;\n  font-size: $size--base;\n  z-index: 10000;\n  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.50);\n  background: $pf-white;\n\n  &--default {\n    color: $pf-charcoal;\n    background-color: $pf-white;\n\n    .menu-item {\n      a {\n        color: inherit;\n      }\n    }\n  }\n\n  &--blog {\n    color: $pf-white;\n    background-color: $pf-charcoal;\n\n    .logo__wrapper {\n      &__text {\n        color: $pf-white;\n      }\n    }\n\n    .menu-item {\n      a {\n        color: $pf-white;\n      }\n    }\n\n    @include media-breakpoint('lg') {\n      .caret {\n        background-image: url('../../../images/icons/caret_white.svg');\n      }\n    }\n  }\n\n  &--front-page {\n    @include media-breakpoint('lg') {\n      box-shadow: none;\n      position: fixed;\n      left: 0;\n      background: none;\n      // transition: background 0.8s ease;\n\n      .header__wrapper {\n        padding-right: 0;\n\n        &__nav {\n          // background: linear-gradient(180deg, #262626 0%, rgba(38, 38, 38, 0.00) 100%);\n\n          @include media-breakpoint('lg') {\n            padding-right: 2rem;\n          }\n        }\n      }\n\n      .menu-item {\n        a {\n          color: $pf-white;\n          // transition: color 0.3s ease;\n        }\n      }\n\n      .caret {\n        background-image: url('../../../images/icons/caret_white.svg');\n      }\n\n      &.default,\n      &.scrolled {\n        color: $pf-charcoal;\n        background-color: $pf-white;\n        box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.50);\n\n        .header__wrapper__nav {\n          background: inherit;\n        }\n\n        .menu-item {\n          a {\n            color: inherit;\n          }\n        }\n\n        .caret {\n          background-image: url('../../../images/icons/caret_charcoal.svg');\n        }\n      }\n    }\n  }\n\n  @include media-breakpoint('lg') {\n    height: $header-height-desktop;\n  }\n\n  &__wrapper {\n    height: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 0 1.125rem;\n\n    @include media-breakpoint('lg') {\n      padding: 0 2rem;\n    }\n\n    &__nav {\n      height: 100%;\n      display: flex;\n      justify-content: flex-end;\n\n      &__inner {\n        height: 100%;\n        display: flex;\n        align-items: center;\n        justify-content: flex-end;\n        width: fit-content;\n      }\n    }\n  }\n\n  .nav {\n\n    /*\n  * MOBILE STYLING\n  */\n    @media screen and (max-width: 991px) {\n      background-color: $pf-raspberry;\n      color: $pf-off-white;\n      position: absolute;\n      left: 0;\n      top: $header-height-mobile;\n      width: 100%;\n      max-height: 0;\n      transition: max-height 1s;\n      overflow: hidden;\n      @include full-bleed;\n\n      &.drop-open {\n        height: fit-content;\n        max-height: 2000px;\n        transition: max-height 1s;\n        box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.50);\n      }\n\n      .main-navigation {\n        margin-top: 2rem;\n      }\n\n      .menu-item {\n        & .sub-menu-btn {\n          display: block;\n\n          &:hover,\n          &:focus {\n            background: $pf-secondary-raspberry;\n          }\n        }\n\n        & .sub-menu {\n          display: unset;\n        }\n\n        a {\n          padding: 1em 2em;\n          display: inline-block;\n          width: 100%;\n          color: inherit;\n\n          &:hover,\n          &:focus,\n          &[aria-expanded='true'] {\n            background: $pf-secondary-raspberry;\n          }\n        }\n      }\n\n      .menu-item-has-children {\n        display: flex;\n        flex-wrap: wrap;\n        align-items: center;\n\n        &>a {\n          flex: 1 0 80%;\n        }\n\n        .sub-menu-btn {\n          flex: 1 0 10%;\n          height: 64px;\n        }\n\n        .sub-menu {\n          overflow: hidden;\n          max-height: 0;\n          transition: max-height 1s;\n          visibility: hidden;\n          flex-basis: 100%;\n\n          a {\n\n            &:hover,\n            &:focus {\n              background: $pf-raspberry;\n              outline-offset: -0.25rem;\n            }\n          }\n        }\n\n        &.open {\n          background: $pf-raspberry;\n\n          .sub-menu {\n            height: fit-content;\n            max-height: 500px;\n            visibility: visible;\n\n            a {\n              padding-left: 4rem;\n            }\n          }\n        }\n      }\n    }\n\n    /*\n  * DESKTOP STYLING\n  */\n    @include media-breakpoint('lg') {\n      display: none;\n      height: 100%;\n      font-weight: 500;\n      text-transform: uppercase;\n\n      ul.main-navigation {\n        height: 100%;\n        display: flex;\n        align-items: center;\n\n        &>.menu-item {\n          height: 100%;\n          display: flex;\n          align-items: center;\n          padding: 0 1rem;\n\n          &:hover,\n          &:focus-within {\n            background-color: $pf-raspberry;\n\n            a {\n              color: $pf-white;\n            }\n\n            .caret {\n              background-image: url('../../../images/icons/caret_white.svg');\n            }\n          }\n        }\n\n        &>li>a {\n          font-weight: 500;\n        }\n      }\n\n      @include media-breakpoint('lg') {\n        display: block;\n      }\n\n      // Has submenu\n      .menu-item-has-children {\n        position: relative;\n        width: fit-content;\n\n        .sub-menu {\n          display: block;\n          visibility: hidden;\n          background-color: $pf-raspberry;\n          position: absolute;\n          left: 50%;\n          margin-left: -150px; // Has to be half the sub-menu width to centre.\n          top: 100%;\n          max-height: 0;\n          width: 300px;\n\n          li {\n\n            &:hover,\n            &:focus {\n              background: $pf-secondary-raspberry;\n              color: $pf-white;\n            }\n\n            a {\n              display: inline-block;\n              padding: 1em 2em;\n              width: 100%;\n            }\n          }\n        }\n\n        &.open,\n        &:hover {\n          color: $pf-off-white;\n          background: $pf-raspberry;\n\n          .caret {\n            pointer-events: none;\n            transform: rotate(180deg);\n            transition: rotate 300ms ease-out;\n            background-image: url('../../../images/icons/caret_white.svg');\n          }\n\n          .sub-menu {\n            transition: all 400ms ease;\n            visibility: visible;\n            max-height: 2000px;\n          }\n        }\n\n        &:hover {\n          .sub-menu-btn {\n            pointer-events: none;\n          }\n        }\n      }\n    }\n\n    /*\n  * CHEVRON BUTTON\n  */\n    .sub-menu-btn {\n      display: flex;\n      align-items: center;\n      height: 100%;\n\n      @include media-breakpoint('lg') {\n        margin-left: 0.25em;\n      }\n\n      &:disabled {\n        cursor: none;\n        pointer-events: none;\n      }\n\n      &[aria-expanded='true'] {\n        .caret {\n          transform: rotate(180deg);\n          transition: rotate 300ms ease-out;\n        }\n      }\n\n      &:hover,\n      &:focus {\n        cursor: pointer;\n        background-color: $pf-raspberry;\n      }\n\n    }\n  }\n\n  /*\n  * HAMBURGER MENU\n  */\n  .hamb {\n    cursor: pointer;\n    padding: 1rem 0 1rem 1rem;\n\n    @include media-breakpoint('lg') {\n      display: none;\n    }\n\n    &.hamb-open {\n      .hamb-line {\n        background: transparent;\n      }\n\n      .hamb-line::before {\n        transform: rotate(-45deg);\n        top: 0;\n      }\n\n      .hamb-line::after {\n        transform: rotate(45deg);\n        top: 0;\n      }\n    }\n\n    .hamb-line {\n      background: $pf-charcoal;\n      display: block;\n      height: 2px;\n      position: relative;\n      width: 24px;\n\n      &::before,\n      &::after {\n        background: $pf-charcoal;\n        content: '';\n        display: block;\n        height: 100%;\n        position: absolute;\n        transition: all 0.2s ease-out;\n        width: 100%;\n      }\n\n      &::before {\n        top: 8px;\n      }\n\n      &::after {\n        top: -8px;\n      }\n    }\n\n    &.header--blog {\n      .hamb-line {\n        background: $pf-white;\n\n        &::before,\n        &::after {\n          background: $pf-white;\n        }\n      }\n\n      &.hamb-open {\n        .hamb-line {\n          background: transparent;\n        }\n      }\n    }\n  }\n\n  &__buttons {\n    display: flex;\n    align-items: center;\n\n    @include media-breakpoint('lg') {\n      margin-left: 1rem;\n    }\n\n    .button--donate {\n      margin-right: 0.5rem;\n    }\n  }\n}","@mixin full-bl